.contacts {
	&__list {
		display: flex;
		align-items: center; }
	&__item {
		display: inline-block;
		margin-left: 20px;
		@media (max-width: 600px) {
			margin-left: 5vw; } }
	&__link {
		font-weight: 600;
		font-size: 14px;
		display: flex;
		align-items: center;
		@media (max-width: 600px) {
			font-size: 0; } }
	&__icon {
		display: none;
		@media (max-width: 600px) {
			display: inline-block;
			width: 5vw;
			height: 5vw; } } }

.footer {
	height: 50px;
	display: flex;
	justify-content: center; }
